import React, { useState, useEffect, createContext } from 'react'

export const NarrowContext = createContext(false)

export const NarrowProvider = ({ limit, children }) => {
    const isClient = typeof window === 'object'

    const isLower = () => isClient ? window.innerWidth < limit : false

    const [narrow, setNarrow] = useState(isLower)

    useEffect(() => {
        if (!isClient) return false

        const handleResize = () => setNarrow(isLower())

        window.addEventListener('resize', handleResize)
        return () => window.removeEventListener('resize', handleResize)
    }, [isClient])

    return (
        <NarrowContext.Provider value={narrow}>
            {children}
        </NarrowContext.Provider>
    )
}
